import moment from "moment";
import React, { useMemo } from "react";

import { extractFontInformation, InnodomBlock } from "@ilabs/ui";
import * as Constants from "../../constants";
import { IDataPointValue, IState, ITaxonomy } from "./types";

const TAG_CONTENT = "inno:content";
const TAG_DATAPOINT = "inno:datapoint";
const TAG_META = "inno:meta";
const ATTR_CONFIDENCE = "confidence";
const ATTR_KEY = "key";
const ATTR_ID = "id";
const ATTR_ID_REF = "idref";

const extractInitialBlocks = (content: Element): IState["blocks"] => {
  const blocks = [];
  const fontInformation = extractFontInformation(content);
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < content.childNodes.length; i++) {
    if (content.childNodes[i] instanceof Element) {
      const childNode = content.childNodes[i] as Element;
      let id = (childNode.getAttribute(ATTR_ID) || "").trim();
      if (id.length < 1) {
        id = `block-${i}`;
        childNode.setAttribute(ATTR_ID, id);
      }
      blocks.push({
        id,
        rendered: <InnodomBlock block={childNode} fontInformation={fontInformation} />,
      });
    }
  }
  return blocks;
};

const extractInitialDataPoints = (meta: Element, taxonomy: ITaxonomy): IState["dataPoints"] => {
  const dataPoints = Object.keys(taxonomy).reduce(
    (prev: IState["dataPoints"], key): IState["dataPoints"] => {
      prev[key] = {
        description: taxonomy[key].description || "",
        key,
        question: taxonomy[key].question || null,
        unclear: false,
        values: [],
      };
      return prev;
    },
    {},
  );
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < meta.childNodes.length; i++) {
    const childNode = meta.childNodes[i];
    if (!(childNode instanceof Element) || childNode.nodeName !== TAG_DATAPOINT) {
      continue;
    }
    const key = (childNode.getAttribute(ATTR_KEY) || "").trim().toLowerCase();
    // ensure the key is in the taxonomy
    if (!(key in dataPoints)) {
      continue;
    }
    let confidence = parseFloat(childNode.getAttribute(ATTR_CONFIDENCE) || "");
    if (isNaN(confidence)) {
      confidence = 0;
    }
    confidence = Math.max(0, Math.min(1, confidence));
    const idRef = (childNode.getAttribute(ATTR_ID_REF) || "").trim();
    let unclear = true;
    let value = null;
    let valueRaw = "";
    if (typeof childNode.textContent === "string") {
      unclear = false;
      valueRaw = childNode.textContent.trim();
      value = null;
      try {
        switch (taxonomy[key].type) {
          case "boolean":
            value = valueRaw.toLowerCase() === "true";
            break;
          case "number":
            value = parseFloat(valueRaw);
            break;
          case "date":
          case "datetime":
          case "time":
            value = moment(valueRaw, Constants.InnodomDateFormats[taxonomy[key].type]).toDate();
            break;
          default:
            value = valueRaw;
            break;
        }
      } catch (err) {
        value = null;
      }
    }
    dataPoints[key] = {
      ...dataPoints[key],
      unclear,
    };
    dataPoints[key].values.push({
      confidence,
      idRef: idRef.length > 0 ? idRef : null,
      value,
      valueRaw,
    });
  }
  return dataPoints;
};

export const getDefaultState = (taxonomy: ITaxonomy, xml: string): IState => {
  const parser = new DOMParser();
  const xmlDoc = parser.parseFromString(xml, "text/xml");
  const errors = xmlDoc.getElementsByTagName("parsererror");
  if (errors.length > 0) {
    throw new Error(`Error parsing XML: ${new XMLSerializer().serializeToString(xmlDoc)}`);
  }
  // return the root of the XML document
  const { documentElement } = xmlDoc;
  if (documentElement && documentElement instanceof Element) {
    let blocks: IState["blocks"] = [];
    let dataPoints: IState["dataPoints"] = {};
    const content = documentElement.getElementsByTagName(TAG_CONTENT);
    if (content.length > 0) {
      blocks = extractInitialBlocks(content[0]);
    }
    const metadata = documentElement.getElementsByTagName(TAG_META);
    if (metadata.length > 0) {
      dataPoints = extractInitialDataPoints(metadata[0], taxonomy);
      // remove the metadata from its parent
      metadata[0].remove();
    }
    return {
      blocks,
      dataPoints,
      selectedDataPoint: null,
      selectedDataPointAnswer: null,
      xmlRoot: xmlDoc,
    };
  }
  throw new Error("Invalid or empty XML.");
};

interface IAddNewAnswerAction {
  key: string;
  type: "ADD NEW ANSWER";
}
interface ISelectBlockAction {
  id: string;
  type: "SELECT BLOCK";
}
interface ISelectQuestionAction {
  key: string;
  type: "SELECT QUESTION";
}
interface IRemoveAnswerAction {
  answer: number;
  key: string;
  type: "REMOVE ANSWER";
}
interface ISelectActiveAnswerAction {
  answer: number;
  key: string;
  type: "SELECT ACTIVE ANSWER";
}
interface IToggleUnclearAction {
  key: string;
  type: "TOGGLE UNCLEAR ANSWER";
}
interface IUpdateAnswerValueAction {
  answer: number;
  key: string;
  type: "UPDATE ANSWER VALUE";
  value: any;
  valueRaw: string;
}

type Action =
  | IAddNewAnswerAction
  | IRemoveAnswerAction
  | ISelectActiveAnswerAction
  | ISelectBlockAction
  | ISelectQuestionAction
  | IToggleUnclearAction
  | IUpdateAnswerValueAction;

// tslint:disable-next-line: cyclomatic-complexity
export const reducer = (state: IState, action: Action): IState => {
  switch (action.type) {
    case "SELECT BLOCK": {
      const { dataPoints, selectedDataPoint, selectedDataPointAnswer } = state;
      if (selectedDataPoint === null) {
        return state;
      }
      const selected = dataPoints[selectedDataPoint];
      if (
        selectedDataPointAnswer === null ||
        !selected ||
        !selected.values[selectedDataPointAnswer]
      ) {
        return state;
      }
      return {
        ...state,
        dataPoints: {
          ...dataPoints,
          [selectedDataPoint]: {
            ...selected,
            values: [
              ...selected.values.slice(0, selectedDataPointAnswer),
              {
                ...selected.values[selectedDataPointAnswer],
                idRef: action.id,
              },
              ...selected.values.slice(selectedDataPointAnswer + 1),
            ],
          },
        },
      };
    }
    case "SELECT QUESTION": {
      if (state.selectedDataPoint === action.key || !state.dataPoints[action.key]) {
        return state;
      }
      let dataPoints = state.dataPoints;
      // if the previous data point has any empty answers, we remove them
      if (state.selectedDataPoint && state.dataPoints[state.selectedDataPoint]) {
        const previousValues = state.dataPoints[state.selectedDataPoint].values.filter(
          v => !!v.valueRaw || !!v.idRef,
        );
        if (previousValues.length < state.dataPoints[state.selectedDataPoint].values.length) {
          dataPoints = {
            ...dataPoints,
            [state.selectedDataPoint]: {
              ...state.dataPoints[state.selectedDataPoint],
              values: previousValues,
            },
          };
        }
      }
      // if the new data point has zero answers, we add a new empty one
      if (state.dataPoints[action.key].values.length < 1) {
        dataPoints = {
          ...dataPoints,
          [action.key]: {
            ...dataPoints[action.key],
            values: [
              {
                confidence: 1.0,
                idRef: null,
                value: "",
                valueRaw: "",
              },
            ],
          },
        };
      }
      return {
        ...state,
        dataPoints,
        selectedDataPoint: action.key,
        selectedDataPointAnswer: 0,
      };
    }
    case "SELECT ACTIVE ANSWER": {
      const { dataPoints, selectedDataPoint, selectedDataPointAnswer } = state;
      if (selectedDataPoint !== action.key || !dataPoints[action.key]) {
        return state;
      }
      if (selectedDataPoint === action.key && selectedDataPointAnswer === action.answer) {
        return state;
      }
      return {
        ...state,
        selectedDataPoint: action.key,
        selectedDataPointAnswer: action.answer,
      };
    }
    case "ADD NEW ANSWER": {
      const { dataPoints, selectedDataPoint } = state;
      if (selectedDataPoint !== action.key || !dataPoints[action.key]) {
        return state;
      }
      return {
        ...state,
        dataPoints: {
          ...dataPoints,
          [action.key]: {
            ...dataPoints[action.key],
            values: dataPoints[action.key].values.concat([
              {
                confidence: 1.0,
                idRef: null,
                value: "",
                valueRaw: "",
              },
            ]),
          },
        },
        selectedDataPoint: action.key,
        selectedDataPointAnswer: dataPoints[action.key].values.length,
      };
    }
    case "REMOVE ANSWER": {
      const { dataPoints, selectedDataPoint } = state;
      if (selectedDataPoint !== action.key || !dataPoints[action.key]) {
        return state;
      }
      if (!dataPoints[action.key].values[action.answer]) {
        return state;
      }
      return {
        ...state,
        dataPoints: {
          ...dataPoints,
          [action.key]: {
            ...dataPoints[action.key],
            values: [
              ...dataPoints[action.key].values.slice(0, action.answer),
              ...dataPoints[action.key].values.slice(action.answer + 1),
            ],
          },
        },
      };
    }
    case "TOGGLE UNCLEAR ANSWER": {
      const { dataPoints, selectedDataPoint } = state;
      if (selectedDataPoint !== action.key || !dataPoints[action.key]) {
        return state;
      }
      return {
        ...state,
        dataPoints: {
          ...dataPoints,
          [action.key]: {
            ...dataPoints[action.key],
            unclear: !dataPoints[action.key].unclear,
          },
        },
      };
    }
    case "UPDATE ANSWER VALUE": {
      const { dataPoints, selectedDataPoint } = state;
      if (selectedDataPoint !== action.key || !dataPoints[action.key]) {
        return state;
      }
      if (!dataPoints[action.key].values[action.answer]) {
        return state;
      }
      return {
        ...state,
        dataPoints: {
          ...dataPoints,
          [action.key]: {
            ...dataPoints[action.key],
            values: [
              ...dataPoints[action.key].values.slice(0, action.answer),
              {
                ...dataPoints[action.key].values[action.answer],
                value: action.value,
                valueRaw: action.valueRaw.trim(),
              },
              ...dataPoints[action.key].values.slice(action.answer + 1),
            ],
          },
        },
      };
    }
    default:
      return state;
  }
};

type Dispatch = (action: Action) => void;
export interface IStateCallbacks {
  onAddNewAnswer: (key: string) => void;
  onRemoveAnswer: (key: string, answer: number) => void;
  onSelectActiveAnswer: (key: string, answer: number) => void;
  onSelectBlock: (id: string) => void;
  onSelectDataPoint: (key: string) => void;
  onToggleAnswerUnclear: (key: string) => void;
  onUpdateAnswerValue: (key: string, answer: number, value: any, valueRaw: string) => void;
}
export const useStateCallbacks = (dispatch: Dispatch) =>
  useMemo(
    (): IStateCallbacks => ({
      onAddNewAnswer: (key: string) => {
        dispatch({
          key,
          type: "ADD NEW ANSWER",
        });
      },
      onRemoveAnswer: (key: string, answer: number) => {
        dispatch({
          answer,
          key,
          type: "REMOVE ANSWER",
        });
      },
      onSelectActiveAnswer: (key: string, answer: number) => {
        dispatch({
          answer,
          key,
          type: "SELECT ACTIVE ANSWER",
        });
      },
      onSelectBlock: (id: string) => {
        dispatch({
          id,
          type: "SELECT BLOCK",
        });
      },
      onSelectDataPoint: (key: string) => {
        dispatch({
          key,
          type: "SELECT QUESTION",
        });
      },
      onToggleAnswerUnclear: (key: string) => {
        dispatch({
          key,
          type: "TOGGLE UNCLEAR ANSWER",
        });
      },
      onUpdateAnswerValue: (key: string, answer: number, value: any, valueRaw: string) => {
        dispatch({
          answer,
          key,
          type: "UPDATE ANSWER VALUE",
          value,
          valueRaw,
        });
      },
    }),
    [dispatch],
  );

export const insertDataPointsToInnodom = (
  root: Document,
  dataPoints: IState["dataPoints"],
  taxonomy: ITaxonomy,
) => {
  const metaTags = root.getElementsByTagName(TAG_META);
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < metaTags.length; i++) {
    metaTags[i].remove();
  }
  const newMetaTag = root.createElement(TAG_META);
  Object.keys(dataPoints).forEach((key: string) => {
    if (dataPoints.unclear) {
      const newDataPointTag = root.createElement(TAG_DATAPOINT);
      newDataPointTag.setAttribute(ATTR_KEY, key);
      newMetaTag.appendChild(newDataPointTag);
      return;
    }
    dataPoints[key].values.forEach((value: IDataPointValue<any>) => {
      const newDataPointTag = root.createElement(TAG_DATAPOINT);
      newDataPointTag.setAttribute(ATTR_KEY, key);
      newDataPointTag.setAttribute(ATTR_CONFIDENCE, value.confidence.toString(10));
      if (value.idRef !== null) {
        newDataPointTag.setAttribute(ATTR_ID_REF, value.idRef);
      }
      newDataPointTag.appendChild(root.createTextNode(value.valueRaw));
      if (key in taxonomy) {
        const { type } = taxonomy[key];
        switch (type) {
          case "date":
          case "datetime":
          case "time":
            newDataPointTag.setAttribute("format", Constants.InnodomDateFormats[type]);
            break;
          default:
            break;
        }
      }
      newMetaTag.appendChild(newDataPointTag);
    });
  });
  root.documentElement.appendChild(newMetaTag);
};

export const areAnswersValid = ({ dataPoints }: IState, taxonomy: ITaxonomy) =>
  useMemo(() => {
    const dataPointKeys = Object.keys(dataPoints);
    for (const dataPointKey of dataPointKeys) {
      console.log(dataPointKey);
      if (!(dataPointKey in taxonomy) || dataPoints[dataPointKey].unclear) {
        continue;
      }
      const taxonomyEntry = taxonomy[dataPointKey];
      if (taxonomyEntry.required && dataPoints[dataPointKey].values.length < 1) {
        return false;
      }
      for (const dataPointValue of dataPoints[dataPointKey].values) {
        if (dataPointValue.idRef === null && taxonomyEntry.requiresSupport) {
          return false;
        } else if (dataPointValue.valueRaw.length < 1 && taxonomyEntry.required) {
          return false;
        }
      }
    }
    return true;
  }, [dataPoints]);



