import React from "react";
import { FormFeedback, Input } from "reactstrap";

import { IInputComponentProps } from "./common";

export const StringInput = ({
  dataPointKey,
  index,
  onFocus,
  onUpdateAnswerValue,
  taxonomyEntry,
  value,
}: IInputComponentProps<string>) => {
  const isSelection = taxonomyEntry.type === "selection";
  const inputId = `input-${dataPointKey}-${index}`;
  const invalid = taxonomyEntry.required && !value.valueRaw;
  const valid = taxonomyEntry.required && !!value.valueRaw;
  const feedbackMessage = isSelection
    ? "Select some text from the document to populate the answer."
    : "Value cannot be left blank.";
  return (
    <>
      <Input
        id={inputId}
        invalid={invalid}
        maxLength={taxonomyEntry.type === "string" ? taxonomyEntry.maxLength : undefined}
        minLength={taxonomyEntry.type === "string" ? taxonomyEntry.minLength : undefined}
        onChange={e => {
          onUpdateAnswerValue(dataPointKey, index, e.target.value, e.target.value);
        }}
        onFocus={() => {
          onFocus(dataPointKey, index);
        }}
        readOnly={isSelection}
        type="text"
        valid={valid}
        value={value.value}
      />
      {invalid && <FormFeedback>{feedbackMessage}</FormFeedback>}
    </>
  );
};
StringInput.displayName = "StringInput";



