import { useEffect } from "react";

import { IState } from "../types";

export const useScrollToBlock = (
  { dataPoints, selectedDataPoint, selectedDataPointAnswer }: IState,
  scrollRef: React.RefObject<HTMLElement>,
) =>
  useEffect(() => {
    if (!scrollRef.current) {
      return;
    }
    if (
      selectedDataPoint !== null &&
      selectedDataPointAnswer !== null &&
      dataPoints[selectedDataPoint] &&
      dataPoints[selectedDataPoint].values[selectedDataPointAnswer] &&
      dataPoints[selectedDataPoint].values[selectedDataPointAnswer].idRef
    ) {
      const block: HTMLElement | null = document.querySelector(
        `#block-${dataPoints[selectedDataPoint].values[selectedDataPointAnswer].idRef}`,
      );
      if (block) {
        scrollRef.current.style.scrollBehavior = "smooth";
        const { clientHeight, offsetTop } = block;
        // if the element is larger than the view port, we scroll to the top of the element
        if (clientHeight > scrollRef.current.clientHeight) {
          scrollRef.current.scrollTo(0, offsetTop);
          return;
        }
        // center the element in the scroll view
        const offsetY = Math.floor(offsetTop - (scrollRef.current.clientHeight - clientHeight) / 2);
        scrollRef.current.scrollTo(
          0,
          Math.max(0, Math.min(scrollRef.current.scrollHeight, offsetY)),
        );
      }
    }
  }, [selectedDataPoint, selectedDataPointAnswer]);



