import React from "react";
import { Col, Form, FormGroup, Input, Label, Row } from "reactstrap";
import { IStateCallbacks } from "../../state";
import { IDataPoint, ITaxonomy } from "../../types";
import { Answers } from "../Answers";

interface IProps {
  readonly dataPoint: IDataPoint<any>;
  readonly index: number;
  stateCallbacks: IStateCallbacks;
  taxonomy: ITaxonomy;
}

export const ActiveQuestion = ({ dataPoint, index, stateCallbacks, taxonomy }: IProps) => {
  const unclearId = `unclear-${dataPoint.key}`;
  return (
    <Form
      onSubmit={e => {
        e.preventDefault();
      }}
    >
      <Row>
        <Col title={dataPoint.description} xs={12}>
          {index}. {dataPoint.question || dataPoint.description || dataPoint.key}
        </Col>
      </Row>
      <Row>
        <Col xs={12}>
          <FormGroup check>
            <Label check htmlFor={unclearId}>
              <Input
                checked={dataPoint.unclear}
                id={unclearId}
                onChange={() => {
                  stateCallbacks.onToggleAnswerUnclear(dataPoint.key);
                }}
                type="checkbox"
              />
              <small>This answer is unclear or not present in the document.</small>
            </Label>
          </FormGroup>
        </Col>
      </Row>
      {!dataPoint.unclear && (
        <Row>
          <Col xs={12}>
            <hr />
            Answers:
            <div className="pl-1 pr-1">
              <Answers dataPoint={dataPoint} stateCallbacks={stateCallbacks} taxonomy={taxonomy} />
            </div>
          </Col>
        </Row>
      )}
    </Form>
  );
};
ActiveQuestion.displayName = "ActiveQuestion";



