import React from "react";
import { Button, Col, FormGroup } from "reactstrap";

import { IDataPoint, ITaxonomy } from "../../types";

import { IStateCallbacks } from "../../state";
import { BooleanInput } from "./BooleanInput";
import { IInputComponentProps } from "./common";
import { DateTimeInput } from "./DateTimeInput";
import { EnumInput } from "./EnumInput";
import { NumberInput } from "./NumberInput";
import { StringInput } from "./StringInput";

interface IProps {
  dataPoint: IDataPoint<any>;
  stateCallbacks: IStateCallbacks;
  taxonomy: ITaxonomy;
}

const inputComponentMap = {
  boolean: BooleanInput,
  date: DateTimeInput,
  datetime: DateTimeInput,
  enum: EnumInput,
  number: NumberInput,
  selection: StringInput,
  string: StringInput,
  time: DateTimeInput,
};

export const Answers = ({
  dataPoint,
  stateCallbacks: { onAddNewAnswer, onRemoveAnswer, onSelectActiveAnswer, onUpdateAnswerValue },
  taxonomy,
}: IProps) => {
  const taxonomyEntry = taxonomy[dataPoint.key];
  return (
    <>
      {dataPoint.values.map((v, i) => {
        const wrapperClassNames = ["datapoint-answer", "pb-3", "pt-3", "mb-0"];
        if (i < dataPoint.values.length - 1) {
          wrapperClassNames.push("border-bottom");
        }
        let alert = null;
        if (v.idRef === null && taxonomyEntry.requiresSupport) {
          alert = (
            <strong className="text-danger">&larr; You must select a supporting text.</strong>
          );
        } else if (v.idRef !== null) {
          alert = (
            <a
              className="text-success"
              onClick={e => {
                e.preventDefault();
                onSelectActiveAnswer(dataPoint.key, i);
              }}
              href="#"
            >
              &larr; Supporting text selected &#x2714;
            </a>
          );
        }
        let inputComponent = null;
        if (taxonomyEntry.type in inputComponentMap) {
          inputComponent = React.createElement<IInputComponentProps<any>>(
            inputComponentMap[taxonomyEntry.type],
            {
              dataPointKey: dataPoint.key,
              index: i,
              onFocus: onSelectActiveAnswer,
              onUpdateAnswerValue,
              taxonomyEntry,
              value: v,
            },
          );
        }
        return (
          <FormGroup className={wrapperClassNames.join(" ")} key={`answer-${i}`} row>
            <Col xs={12}>
              <div className="d-flex align-items-top mb-1">
                <div className="flex-fill mr-2">{inputComponent}</div>
                <a
                  className="remove-link"
                  href="#"
                  onClick={e => {
                    e.preventDefault();
                    onRemoveAnswer(dataPoint.key, i);
                  }}
                >
                  &times;
                </a>
              </div>
              {alert}
            </Col>
          </FormGroup>
        );
      })}
      <FormGroup row>
        <Col className="text-center" xs={12}>
          <Button
            color="primary"
            onClick={() => {
              onAddNewAnswer(dataPoint.key);
            }}
            type="button"
          >
            &#x271A; Add
          </Button>
        </Col>
      </FormGroup>
    </>
  );
};
Answers.displayName = "Answers";



