import React from "react";
import { ListGroupItem } from "reactstrap";

import { IStateCallbacks } from "../../state";
import { IDataPoint, ITaxonomy } from "../../types";
import { ActiveQuestion } from "./ActiveQuestion";
import { InactiveQuestion } from "./InactiveQuestion";

interface IProps {
  active: boolean;
  dataPoint: IDataPoint<any>;
  index: number;
  stateCallbacks: IStateCallbacks;
  taxonomy: ITaxonomy;
}

export const Question = ({ active, dataPoint, index, stateCallbacks, taxonomy }: IProps) => (
  <ListGroupItem
    onClick={() => {
      stateCallbacks.onSelectDataPoint(dataPoint.key);
    }}
  >
    {active ? (
      <ActiveQuestion
        dataPoint={dataPoint}
        index={index}
        stateCallbacks={stateCallbacks}
        taxonomy={taxonomy}
      />
    ) : (
      <InactiveQuestion
        dataPoint={dataPoint}
        index={index}
        taxonomyEntry={taxonomy[dataPoint.key]}
      />
    )}
  </ListGroupItem>
);
Question.displayName = "Question";



