import React from "react";
import { Button, ButtonGroup } from "reactstrap";

import { IInputComponentProps } from "./common";

export const BooleanInput = ({
  dataPointKey,
  index,
  onFocus,
  onUpdateAnswerValue,
  taxonomyEntry,
  value,
}: IInputComponentProps<boolean>) => {
  const inputId = `input-${dataPointKey}-${index}`;
  const invalid = taxonomyEntry.required && value.value !== true && value.value !== false;
  return (
    <>
      <ButtonGroup className="boolean-input" id={inputId}>
        <Button
          color="primary"
          onClick={() => {
            onFocus(dataPointKey, index);
            onUpdateAnswerValue(dataPointKey, index, true, "true");
          }}
          outline={value.value !== true}
          type="button"
        >
          Yes
        </Button>
        <Button
          color="primary"
          onClick={() => {
            onFocus(dataPointKey, index);
            onUpdateAnswerValue(dataPointKey, index, false, "false");
          }}
          outline={value.value !== false}
          type="button"
        >
          No
        </Button>
      </ButtonGroup>
      {invalid && <small className="text-danger">You must select an option.</small>}
    </>
  );
};
BooleanInput.displayName = "BooleanInput";



