import moment from "moment";
import React, { memo, useRef } from "react";

import { InnodomDateFormats, WorkbenchDateFormats } from "../../../constants";
import { IStateCallbacks } from "../state";
import { IState, ITaxonomy } from "../types";
import { Block } from "./Block";
import { TextSelection } from "./TextSelection";
import { useScrollToBlock } from "./useScrollToBlock";

interface IProps {
  state: IState;
  stateCallbacks: IStateCallbacks;
  taxonomy: ITaxonomy;
}

export const Blocks = memo(({ state, stateCallbacks, taxonomy }: IProps) => {
  const scrollRef = useRef(null);
  useScrollToBlock(state, scrollRef);
  const { blocks, dataPoints, selectedDataPoint, selectedDataPointAnswer } = state;
  const wrapperClassnames = ["blocks", "h-100", "overflow-auto", "p-3"];
  if (
    selectedDataPoint !== null &&
    selectedDataPointAnswer !== null &&
    !dataPoints[selectedDataPoint].unclear
  ) {
    wrapperClassnames.push("selecting");
  }
  return (
    <div className={wrapperClassnames.join(" ")} ref={scrollRef}>
      <TextSelection
        onSelectText={text => {
          if (
            selectedDataPoint !== null &&
            selectedDataPointAnswer !== null &&
            !dataPoints[selectedDataPoint].unclear &&
            selectedDataPoint in taxonomy
          ) {
            const { type } = taxonomy[selectedDataPoint];
            switch (type) {
              // we only support text selection for these types
              case "number": {
                const value = parseFloat(text.replace(/\s|\$|%|,/g, ""));
                if (isNaN(value)) {
                  stateCallbacks.onUpdateAnswerValue(
                    selectedDataPoint,
                    selectedDataPointAnswer,
                    null,
                    "",
                  );
                  return;
                }
                stateCallbacks.onUpdateAnswerValue(
                  selectedDataPoint,
                  selectedDataPointAnswer,
                  value,
                  value.toString(10),
                );
                break;
              }
              case "date":
              case "datetime":
              case "time": {
                const parsed = moment(text, WorkbenchDateFormats.allSupported);
                if (parsed.isValid()) {
                  stateCallbacks.onUpdateAnswerValue(
                    selectedDataPoint,
                    selectedDataPointAnswer,
                    parsed.toDate(),
                    parsed.format(InnodomDateFormats[type]),
                  );
                }
                break;
              }
              case "selection":
              case "string":
                stateCallbacks.onUpdateAnswerValue(
                  selectedDataPoint,
                  selectedDataPointAnswer,
                  text,
                  text,
                );
                break;
            }
          }
        }}
      >
        {blocks.map((b, i) => {
          const blockId = `block-${b.id}`;
          return (
            <Block
              block={b}
              blockId={blockId}
              key={blockId}
              state={state}
              stateCallbacks={stateCallbacks}
            />
          );
        })}
      </TextSelection>
    </div>
  );
});
Blocks.displayName = "Blocks";



