import React from "react";
import { FormFeedback, Input } from "reactstrap";

import { IInputComponentProps } from "./common";

export const NumberInput = ({
  dataPointKey,
  index,
  onFocus,
  onUpdateAnswerValue,
  taxonomyEntry,
  value,
}: IInputComponentProps<number>) => {
  const inputId = `input-${dataPointKey}-${index}`;
  let invalid = false;
  let errorMessage = null;
  if (typeof value.value !== "number" || isNaN(value.value)) {
    invalid = true;
    errorMessage = "Value must be a valid number.";
  } else if (taxonomyEntry.required && value.valueRaw.length < 1) {
    invalid = true;
    errorMessage = "Value cannot be left blank.";
  }
  return (
    <>
      <Input
        id={inputId}
        invalid={invalid}
        max={taxonomyEntry.type === "number" ? taxonomyEntry.max : undefined}
        min={taxonomyEntry.type === "number" ? taxonomyEntry.min : undefined}
        onChange={e => {
          onUpdateAnswerValue(dataPointKey, index, parseFloat(e.target.value), e.target.value);
        }}
        onFocus={() => {
          onFocus(dataPointKey, index);
        }}
        step="any"
        type="number"
        valid={!invalid}
        value={value.value !== null ? value.value.toString(10) : ""}
      />
      {invalid && <FormFeedback>{errorMessage}</FormFeedback>}
    </>
  );
};
NumberInput.displayName = "NumberInput";



