import React from "react";

import { IStateCallbacks } from "../state";
import { IBlock, IState } from "../types";

interface IProps {
  block: IBlock;
  blockId: string;
  state: IState;
  stateCallbacks: IStateCallbacks;
}

export const Block = ({
  block,
  blockId,
  state: { dataPoints, selectedDataPoint, selectedDataPointAnswer },
  stateCallbacks: { onSelectBlock },
}: IProps) => {
  let selected = false;
  if (
    selectedDataPoint !== null &&
    selectedDataPointAnswer !== null &&
    !!dataPoints[selectedDataPoint] &&
    !dataPoints[selectedDataPoint].unclear &&
    !!dataPoints[selectedDataPoint].values[selectedDataPointAnswer]
  ) {
    selected = dataPoints[selectedDataPoint].values[selectedDataPointAnswer].idRef === block.id;
  }
  const blockClasses = ["block"];
  if (selected) {
    blockClasses.push("selected");
  }
  return (
    <div
      className={blockClasses.join(" ")}
      id={blockId}
      onClick={() => {
        if (
          selectedDataPoint !== null &&
          selectedDataPointAnswer !== null &&
          !!dataPoints[selectedDataPoint] &&
          !dataPoints[selectedDataPoint].unclear &&
          !!dataPoints[selectedDataPoint].values[selectedDataPointAnswer]
        ) {
          const { idRef } = dataPoints[selectedDataPoint].values[selectedDataPointAnswer];
          if (idRef !== null && idRef === block.id) {
            return;
          }
          if (idRef === null) {
            onSelectBlock(block.id);
            return;
          }
          const confirmation = window.confirm(
            "Are you sure you want to update the supporting text for this answer?",
          );
          if (confirmation) {
            onSelectBlock(block.id);
          }
        }
      }}
    >
      {block.rendered}
    </div>
  );
};
Block.displayName = "Block";



