import moment from "moment";
import React from "react";
import { DateTimePicker } from "react-widgets";

import { InnodomDateFormats, WorkbenchDateFormats } from "../../../../constants";
import { IInputComponentProps } from "./common";

export const DateTimeInput = ({
  dataPointKey,
  index,
  onFocus,
  onUpdateAnswerValue,
  taxonomyEntry,
  value,
}: IInputComponentProps<Date>) => {
  const format =
    taxonomyEntry.type in WorkbenchDateFormats
      ? WorkbenchDateFormats[taxonomyEntry.type]
      : WorkbenchDateFormats.datetime;
  const invalid = taxonomyEntry.required && !(value.value instanceof Date);
  const dateValue = value.value && value.value instanceof Date ? value.value : undefined;
  return (
    <>
      <DateTimePicker
        date={taxonomyEntry.type === "date" || taxonomyEntry.type === "datetime"}
        format={format}
        onChange={(date: Date) => {
          let dateFormat = InnodomDateFormats.datetime;
          if (taxonomyEntry.type in InnodomDateFormats) {
            dateFormat = InnodomDateFormats[taxonomyEntry.type];
          }
          onUpdateAnswerValue(dataPointKey, index, date, moment(date).format(dateFormat));
        }}
        onFocus={() => {
          onFocus(dataPointKey, index);
        }}
        max={
          taxonomyEntry.type === "date" || taxonomyEntry.type === "datetime"
            ? taxonomyEntry.max
            : undefined
        }
        min={
          taxonomyEntry.type === "date" || taxonomyEntry.type === "datetime"
            ? taxonomyEntry.min
            : undefined
        }
        parse={str => {
          const parsed = moment(str, WorkbenchDateFormats.allSupported);
          if (parsed.isValid()) {
            return parsed.toDate();
          }
          setTimeout(() => {
            onUpdateAnswerValue(dataPointKey, index, undefined, "");
          }, 0);
          return new Date();
        }}
        placeholder={format}
        time={taxonomyEntry.type === "time" || taxonomyEntry.type === "datetime"}
        value={dateValue}
      />
      {invalid && <small className="text-danger">A value must be selected.</small>}
    </>
  );
};
DateTimeInput.displayName = "DateTimeInput";



