import React from "react";
import { IDataPoint, TaxonomyEntry } from "../../types";

interface IProps {
  dataPoint: IDataPoint<any>;
  index: number;
  taxonomyEntry: TaxonomyEntry;
}

export const InactiveQuestion = ({ dataPoint, index, taxonomyEntry }: IProps) => {
  const contentClasses = ["align-items-center", "d-flex"];
  let details = null;
  if (dataPoint.unclear) {
    details = <small className="text-muted">Unclear</small>;
  } else if (dataPoint.values.length > 0) {
    const missingSupport = dataPoint.values.filter(v => v.idRef === null);
    const emptyValues = dataPoint.values.filter(v => v.valueRaw.trim().length < 1);
    if (taxonomyEntry.requiresSupport && missingSupport.length > 0) {
      contentClasses.push("text-danger");
      details = <small>&#x2731; Requires support</small>;
    } else if (taxonomyEntry.required && emptyValues.length > 0) {
      contentClasses.push("text-danger");
      details = <small>&#x2731; Required</small>;
    } else {
      contentClasses.push("text-success");
      const text = `${dataPoint.values.length} answer${dataPoint.values.length > 1 ? "s" : ""}`;
      details = <small>&#x2714; {text}</small>;
    }
  } else if (taxonomyEntry.required) {
    contentClasses.push("text-danger");
    details = <small>&#x2731; Required</small>;
  }
  return (
    <div className={contentClasses.join(" ")}>
      <div className="flex-fill text-truncate">
        <span title={dataPoint.description}>
          {index}. {dataPoint.question || dataPoint.description || dataPoint.key}
        </span>
      </div>
      {details}
    </div>
  );
};
InactiveQuestion.displayName = "InactiveQuestion";



